
@charset "UTF-8";
.menu-bar ul {
  list-style: none;
  margin: 0;
}
.menu-bar details {
  padding: 10px;
}
.menu-bar summary {
  padding: 5px 0;
  border-bottom: 1px solid #f5f5f5;
}
.menu-bar summary .svg-icon {
    margin-right: 5px;
}
.widget-list {
  padding: 0;
}
.menu-item {
  display: inline-block;
  text-align: center;
  width: 50px;
  cursor: pointer;
  padding: 15px 0 8px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  position: relative;
}
.menu-item:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}
.menu-item .svg-icon {
    font-size: 24px;
}
.menu-item .menu-caption {
    display: block;
    font-size: 12px;
    width: 100%;
}
.layer-list {
  padding: 10px;
}
.layer-list li {
    text-align: left;
    cursor: pointer;
    white-space: nowrap;
    line-height: 24px;
    padding-left: 5px;
}
.layer-list li:hover {
      background: #f5f5f5;
}
.layer-list li::before {
      content: 'â€º ';
}
.layer-list li.layer-active {
      color: #fff;
      background: #000;
}



