
.navbar {
  background: #000;
}
.navbar .svg-icon svg {
    vertical-align: middle;
}
.navbar .btn.btn-link {
    color: #acb3c2;
    margin-right: 15px;
}
.navbar .btn.btn-link:hover {
    color: #fff;
}
.navbar .logo {
    padding: 10px 0 10px 10px;
    font-size: 20px;
}
.navbar .logo .svg-icon {
      width: 30px;
      text-align: center;
      background-color: #fff;
      border-radius: 50%;
}



