
.toast {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 70px;
  width: auto;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.drop-enter, .drop-leave-active {
  opacity: 0;
  top: 60px;
}



